import React from "react";
import { Link, useLocation } from "react-router-dom"; // useLocation 
import "./Navbar.css";

const Navbar = ({ isVisible, setIsVisible }) => {
  const location = useLocation(); //   

  const isMobile = window.innerWidth <= 768; //    

  const isClicked = () => {
    if (isMobile) {
      setIsVisible(false); //  Navbar 
    }
  };

  return (
    <div className={`navbar ${isVisible ? "visible" : "hidden"}`}>
      <Link to="/" onClick={isClicked}>
        <h2>GIANT ERP</h2>
      </Link>
      <div className="navbar-buttons">
        <Link
          to="/emp"
          className={`navbar-item ${location.pathname === "/emp" ? "active" : ""}`} //    active  
          onClick={isClicked}
        >
          
        </Link>
        <Link
          to="/item"
          className={`navbar-item ${location.pathname === "/item" ? "active" : ""}`} //    active  
          onClick={isClicked}
        >
          
        </Link>
        <Link
          to="/sales"
          className={`navbar-item ${location.pathname === "/sales" ? "active" : ""}`} //    active  
          onClick={isClicked}
        >
          
        </Link>
      </div>
    </div>
  );
};

export default Navbar;
