import React from "react";
import { Link } from "react-router-dom";
import "./Header.css";

const Header = ({ toggleNavbar, isNavVisible, isLoggedIn, userStatus, handleLogout, setShowLoginModal }) => {
  return (
    <header className="header">
      <h1 className="header-title">
        <Link to="/" className="Header-button">My Application</Link>
      </h1>

      
      {/*      */}
      <div className="login-section">
        {isLoggedIn ? (
          <div className="loginState">
            <p>, {localStorage.getItem("userId")}! ({userStatus})</p>
            <button className="header-button" onClick={handleLogout}></button>
          </div>
        ) : (<div className="loginState">
              <button className="header-button" onClick={() => setShowLoginModal(true)}></button>
          </div>
      )}
      </div>
      <button className="navbar-toggle header-button" onClick={toggleNavbar}>
        {isNavVisible ? "" : ""}
      </button>
    </header>
  );
};

export default Header;
