import React, { useState } from "react";
import "./Purchase.css"; //    CSS
import * as PortOne from "@portone/browser-sdk/v2";

const Purchase = ({ item, closeModal }) => {
  //   
  const [quantity, setQuantity] = useState(1);

  if (!item) return null; // item    

  const handleQuantityChange = (e) => {
    const value = e.target.value;
  
    //   
    if (value === "") {
      setQuantity("");
      return;
    }
  
    //   
    const newQuantity = Math.min(Math.max(1, Number(value)), item.itemQuantity);
    setQuantity(newQuantity);
  };
  
  //     
  const handleQuantityBlur = () => {
    if (quantity === "" || quantity < 1) {
      setQuantity(1); // 
    }
  };

  const handlePayment = async () => {
    try {
      const response = await PortOne.requestPayment({
        storeId: "store-3f9ebc91-a91b-4783-8f05-da9c39896c3a",
        channelKey: "channel-key-08beca98-f344-4692-9edc-78c5b0006ee7",
        paymentId: `payment-${crypto.randomUUID()}`,
        orderName: item.itemName,
        totalAmount: item.price * quantity, //  * 
        currency: "CURRENCY_KRW",
        payMethod: "EASY_PAY", //   
        redirectUrl: "https://hancheolkim.github.io/react_real/#/item"
      });
  
  
      //    
      if (response.code !== undefined) {
        return alert(response.message); //   
      }
  
      //    
      const userNum = localStorage.getItem("userNum");
      const userId = localStorage.getItem("userId");
  
      //   ,       
      const notified = await fetch(`https://n0b85a7897a3e9c3213c819af9d418042.apppaas.app/payment/complete`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "Authorization": "PortOne utFxMAsGVKNYWxXKECqU3WTkGrwnLnSjLuHbrhtds3MxieauTc9YSzEuxaycVPHAxl2DHGJnTiEkKdBS", //    
        },
        body: JSON.stringify({
          paymentId: response.paymentId,  //  ID
          itemNum: item.itemNum,       // 
          totalPrice: item.price * quantity, //  * 
          totalCostPrice: item.costPrice * quantity, //  * 
          salesQuantity : quantity, // 
          payType: "Easy_Pay",
          userNum, //   
          userId,  //  ID 
        }),
      });
  
      const result = await notified.json();
      if (result.success) {
        alert(" .");
        closeModal();
        window.location.reload();  //  
    
      } else {
        alert("  .");
      }
    } catch (error) {
      console.error(" :", error);
      alert("    .");
    }
  };

  return (
    <div className="modal-overlay">
      <div className="modal-content">
        <h2> </h2>
        <div className="align-left">
          <p><strong> : </strong> {item.itemName}</p>
          <p><strong> : </strong> {item.price.toLocaleString()} </p>
          <div>
            <strong> : </strong>
            <input
              type="number"
              value={quantity}
              onChange={handleQuantityChange}
              onBlur={handleQuantityBlur} //    
              className="quantity-input"
              />  
            <span className="perchase-quantity">&nbsp;({item.itemQuantity}  )</span>
        </div>
          <span className="red-text">*   .</span>
        </div>
        <hr></hr>
        <div>
        <strong>  : {(item.price * quantity).toLocaleString()}</strong>
        </div>
        <div>
          <button onClick={handlePayment} className="perchase-button"></button>
          <button onClick={closeModal} className="close perchase-button"></button>
        </div>
      </div>
    </div>
  );
};

export default Purchase;
