import React, { useState } from "react";
import "./LoginModal.css"; //  

const LoginModal = ({ onClose }) => {
  const [id, setId] = useState("");
  const [password, setPassword] = useState("");
  const [idError, setIdError] = useState("");
  const [passwordError, setPasswordError] = useState("");

  const handleLogin = async () => {
    let hasError = false;

    //  
    if (!id) {
      setIdError(" .");
      hasError = true;
    }

    //  
    if (!password) {
      setPasswordError(" .");
      hasError = true;
    }

    if (hasError) return;

    try {
      const response = await fetch("https://n0b85a7897a3e9c3213c819af9d418042.apppaas.app/user/login", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ id, password }),
      });

      const data = await response.json();

      if (data.success) {
        localStorage.setItem("userNum", data.user_num);
        localStorage.setItem("userId", data.user_id);
        localStorage.setItem("userStatus", data.user_status);

        onClose();
        window.location.reload();
      } else {
        alert("   .");
      }
    } catch (error) {
      console.error(error);
      alert("    .  .");
    }
  };

  const handleIdChange = (e) => {
    const value = e.target.value;
    setId(value);

    //    
    if (!/^[a-zA-Z0-9]*$/.test(value)) {
      setIdError(" ,  .");
    } else {
      setIdError(""); //  
    }
  };

  return (
    <div className="modal-overlay">
      <div className="modal">
        <h2></h2>
        <div className="input-container">
        <input
          type="text"
          placeholder=""
          value={id}
          onChange={handleIdChange}
          className="input-field"
        />
          {idError && <p className="error-text">{idError}</p>}
        </div>
        <div className="input-container">
        <input
          type="password"
          placeholder=""
          value={password}
          onChange={(e) => {
            setPassword(e.target.value);
            setPasswordError(""); //  
          }}
          className="input-field"
        />
          {passwordError && <p className="error-text">{passwordError}</p>}
        </div>
        <button onClick={handleLogin} className="login-button"></button>
        <button onClick={onClose} className="cancle-button"></button>
      </div>
    </div>
  );
};

export default LoginModal;
